"use client";

import type { RefObject } from 'react';
import { Image as ImageIcon, Star, Zap, Heart, Cloud, Sparkles, Smile, Download } from 'lucide-react';
import { toPng } from 'html-to-image';
import { cn } from '@/lib/utils';

type PostTemplateProps = {
  title: string;
  subtitle: string;
  color: 'orange' | 'blue' | 'purple' | 'green';
  image: string | null;
  imageFit?: 'cover' | 'contain' | 'fill';
  imagePosition?: { x: number; y: number };
  imageZoom?: number;
  imageRotation?: number;
  reference: RefObject<HTMLDivElement>;
};

// Renk Temalarƒ± (Pastel & Soft)
const themes = {
  orange: {
    bg: "bg-[#FFF8F0]",
    gradient: "from-[#FFF8F0] via-[#FFE4D6] to-[#FFD1BC]",
    accent: "text-[#FF8A65]",
    pill: "bg-[#FF8A65]/10 text-[#FF8A65] border-[#FF8A65]/20",
    icon: Star,
    decor: "text-[#FFCCBC]",
  },
  blue: {
    bg: "bg-[#F0F7FF]",
    gradient: "from-[#F0F7FF] via-[#E1F0FF] to-[#C3E3FF]",
    accent: "text-[#4FC3F7]",
    pill: "bg-[#4FC3F7]/10 text-[#039BE5] border-[#4FC3F7]/20",
    icon: Cloud,
    decor: "text-[#B3E5FC]",
  },
  purple: {
    bg: "bg-[#FAF5FF]",
    gradient: "from-[#FAF5FF] via-[#F3E5F5] to-[#E1BEE7]",
    accent: "text-[#BA68C8]",
    pill: "bg-[#BA68C8]/10 text-[#8E24AA] border-[#BA68C8]/20",
    icon: Heart,
    decor: "text-[#E1BEE7]",
  },
  green: {
    bg: "bg-[#F1F8E9]",
    gradient: "from-[#F1F8E9] via-[#DCEDC8] to-[#C5E1A5]",
    accent: "text-[#81C784]",
    pill: "bg-[#81C784]/10 text-[#558B2F] border-[#81C784]/20",
    icon: Zap,
    decor: "text-[#C8E6C9]",
  },
};

export function PostTemplate({
  reference,
  title,
  subtitle,
  color,
  image,
  imageFit = 'cover',
  imagePosition = { x: 50, y: 50 },
  imageZoom = 1,
  imageRotation = 0,
}: PostTemplateProps) {
  const currentTheme = themes[color];
  const Icon = currentTheme.icon;

  const handleDownload = async () => {
    if (reference.current) {
      try {
        const dataUrl = await toPng(reference.current, {
          pixelRatio: 2,
          filter: (node: any) => !node.classList?.contains('exclude-from-export'),
        });
        const link = document.createElement('a');
        link.download = `${title.replace(/\s+/g, '-').toLowerCase()}-post.png`;
        link.href = dataUrl;
        link.click();
      } catch (err) {
        console.error('Hata:', err);
      }
    }
  };

  return (
    <div ref={reference} className="w-[1080px] h-[1350px] flex-shrink-0 relative overflow-hidden font-headline selection:bg-none group">
      
      {/* 1. KATMAN: Arka Plan ve Atmosfer */}
      <div className={cn("absolute inset-0 flex flex-col items-center bg-gradient-to-br", currentTheme.bg, currentTheme.gradient)}>
        
        {/* Yumu≈üak Desen (Grid) */}
        <div className="absolute inset-0 opacity-[0.15]" 
             style={{ backgroundImage: 'radial-gradient(#000 1px, transparent 1px)', backgroundSize: '40px 40px' }}>
        </div>

        {/* B√ºy√ºk Yumu≈üak I≈üƒ±klar (Blobs) */}
        <div className="absolute top-[-10%] left-[-10%] w-[800px] h-[800px] bg-white/60 rounded-full blur-[120px] mix-blend-overlay"></div>
        <div className="absolute bottom-[-10%] right-[-10%] w-[600px] h-[600px] bg-white/40 rounded-full blur-[100px] mix-blend-overlay"></div>

        {/* 2. KATMAN: Dekoratif ƒ∞konlar (Pastel) */}
        <div className="absolute top-32 left-16 transform -rotate-12 opacity-80">
           <Icon size={140} className={cn("drop-shadow-xl", currentTheme.decor)} strokeWidth={2.5} />
        </div>
        <div className="absolute top-48 right-20 transform rotate-12 opacity-60">
           <Sparkles size={100} className={cn("drop-shadow-lg", currentTheme.decor)} />
        </div>
        <div className="absolute bottom-40 left-24 transform -rotate-45 opacity-70">
           <Smile size={110} className={cn("drop-shadow-lg", currentTheme.decor)} strokeWidth={2} />
        </div>

        {/* 3. KATMAN: Ana ƒ∞√ßerik */}
        <div className="relative z-20 w-full h-full flex flex-col items-center justify-between py-16 px-12">
          
          {/* BA≈ûLIK ALANI */}
          <div className="text-center flex flex-col items-center gap-6 mt-4">
            <div className={cn("px-8 py-2 rounded-full border backdrop-blur-sm shadow-sm", currentTheme.pill)}>
               <p className="font-bold text-2xl tracking-widest uppercase">
                 {subtitle}
               </p>
            </div>
            
            <h1 className={cn("text-[5.5rem] leading-[1] font-black tracking-tight drop-shadow-sm", currentTheme.accent)}>
              {title}
            </h1>
          </div>

          {/* TELEFON ALANI (Merkezde ve Tam G√∂r√ºn√ºr) */}
          <div className="flex-1 w-full flex items-center justify-center relative my-8">
             
             {/* Arkadaki Buzlu Cam Kart (√áer√ßeve) */}
             <div className="absolute w-[600px] h-[850px] bg-white/30 backdrop-blur-xl rounded-[80px] border border-white/50 shadow-2xl transform rotate-[-3deg]"></div>
             
             {/* G√∂lge */}
             <div className="absolute bottom-0 w-[400px] h-[40px] bg-black/20 blur-[50px] rounded-[100%]"></div>

             {/* TELEFON KASASI */}
             <div className="relative w-[420px] h-[860px] bg-black rounded-[56px] border-[12px] border-gray-800 shadow-2xl overflow-hidden ring-1 ring-gray-900/50 z-10">
                <div className="w-full h-full bg-black overflow-hidden relative">
                  {image ? (
                    <img
                      src={image}
                      className="w-full h-full"
                      alt="App Screen"
                      style={{ 
                        objectFit: imageFit,
                        objectPosition: `${imagePosition.x}% ${imagePosition.y}%`,
                        transform: `scale(${imageZoom}) rotate(${imageRotation}deg)`
                      }}
                    />
                  ) : (
                    <div className="w-full h-full flex flex-col items-center justify-center bg-gray-900">
                       <ImageIcon size={64} className="text-gray-600 mb-4" />
                       <span className="text-gray-500 font-bold text-xl">G√∂rsel Y√ºkle</span>
                    </div>
                  )}

                  {/* Parlama Efekti */}
                  <div className="absolute inset-0 bg-gradient-to-tr from-white/10 via-transparent to-transparent opacity-40 pointer-events-none z-50"></div>
                </div>
             </div>
          </div>

          {/* ALT Bƒ∞LGƒ∞ (Logo) */}
          <div className="flex items-center gap-3 opacity-80 mb-4">
             <div className="w-10 h-10 bg-white rounded-xl flex items-center justify-center shadow-sm text-xl">ü¶ä</div>
             <span className={cn("font-bold text-2xl tracking-widest", currentTheme.accent)}>KIDSAI</span>
          </div>

        </div>

        {/* ƒ∞ndirme Butonu */}
        <button
          onClick={handleDownload}
          className="exclude-from-export absolute top-8 right-8 z-50 bg-white/40 hover:bg-white/60 text-gray-700 p-4 rounded-full backdrop-blur-md transition-all opacity-0 group-hover:opacity-100 shadow-xl border border-white/50 translate-y-[-10px] group-hover:translate-y-0 duration-300"
          title="Kaydet"
        >
          <Download size={28} />
        </button>

      </div>
    </div>
  );
}